<template>
  <a-form-model
    ref="form"
    :model="entity"
    layout="vertical"
    autocomplete="off"
    class="h-father"
  >
    <g-body-form-layout>
      <!-- 搜索插槽 -->
      <template #search-card>
        <div class="search-card">
          <g-container-list
            v-model="entity.mainContainer"
            @change="onSearch"
          >
          </g-container-list>
        </div>
      </template>

      <!-- 展开插槽 -->
      <template #expand-card>
        <div class="expand-card">
          <g-collapse headerName="批次信息">
            <template #content>
              <!-- 这里显示主批次信息，可以使用 g-container-basic-info 组件 -->
              {{entity}}
            </template>
          </g-collapse>
        </div>
      </template>

      <template #card-body>
        <a-row :gutter="24">
          <a-col :span="24">
            <g-collapse headerName="待解除关联批次">
              <template #content>
                <a-input
                  v-model="searchContainer"
                  placeholder="输入批次号快速选择"
                  @pressEnter="addByContainer"
                  style="margin-bottom: 10px;"
                />
                <a-table
                  :columns="relatedColumns"
                  :data-source="relatedContainers"
                  :row-key="(record) => record.containerName"
                  :row-selection="{
                    selectedRowKeys: setOfCheckedId,
                    onChange: onSelectChange,
                  }"
                  :pagination="false"
                  bordered
                  size="small"
                >
                </a-table>
              </template>
            </g-collapse>
          </a-col>
        </a-row>
      </template>

      <template #g-fixed-row>
        <div class="g-fixed-row">
          <g-btn size="large" text="刷新" @callback="refresh"></g-btn>
          <g-btn
            size="large"
            text="解除关联"
            type="primary"
            @callback="removeRelation"
          ></g-btn>
        </div>
      </template>
    </g-body-form-layout>
  </a-form-model>
</template>

<script>

export default {
  name: 'DataV2ContainerRemoveRelation',

  data() {
    return {
      entity: {
        mainContainer: '', // 主批次
      },
      relatedContainers: [], // 待解除关联批次列表
      setOfCheckedId: [], // 选中的待解除关联批次ID
      searchContainer:'',
      relatedColumns: [
        {
          title: '批次号',
          dataIndex: 'containerName',
        },
        {
          title: '状态',
          dataIndex: 'status',
        },
      ],
    };
  },

  mounted() {},

  methods: {
    addByContainer() {
        // 实现搜索表格勾选
        const container = this.relatedContainers.find(
          (item) => item.containerName === this.searchContainer.trim()
        );
        if (container) {
          if (!this.setOfCheckedId.includes(container.containerName)) {
            this.setOfCheckedId.push(container.containerName);
          }
          this.searchContainer = '';
        } else {
          this.$message.warning('该批次不在待解除关联列表中');
        }
    },
    // 选择主批次
    onSearch(value) {
      this.entity.mainContainer = value;
      this.setOfCheckedId = [];
      this.relatedContainers = [];


      if (value) {
        this.getRelationContainer(value);
      }
    },

    // 获取已关联批次列表
    async getRelationContainer(containerName) {
      // TODO: 调用 API 获取已关联批次列表
      // 模拟数据
      const res = {
        output: {
          relationContainerList: [
            { containerName: 'B001', status: '已入库' },
            { containerName: 'B002', status: '已入库' },
          ],
        },
      };
      if (res && res.output) {
        this.relatedContainers = res.output.relationContainerList || [];
      }
    },

    // 选择待解除关联批次
    onSelectChange(selectedRowKeys) {
      this.setOfCheckedId = selectedRowKeys;
    },

    // 解除关联批次
    removeRelation() {
      if (!this.entity.mainContainer) {
        this.$message.warning('请选择主批次');
        return;
      }
      if (this.setOfCheckedId.length === 0) {
        this.$message.warning('请选择要解除关联的批次');
        return;
      }

      // TODO: 调用 API 进行解除关联
      // ...

      this.$message.success('解除关联成功');
      this.refresh();
    },

    // 刷新
    refresh() {
      this.onSearch(this.entity.mainContainer);
    },
  },
};
</script>

<style lang="scss" scoped>
.search-card {
  margin-bottom: 16px;
}

.g-fixed-row {
  display: flex;
  justify-content: flex-end;
  gap: 8px;
}
</style>